.empty-list{
    display: flex;
    justify-content: center;
    font-size: 32px;
    color: white;
    background: rgba(0, 0, 0, 0.85);
    padding: 12px;
    margin-inline: 2%;
    border-radius: 6px;
}
.empty-list > div > p {
    margin: 0;
    padding: 0;
    text-align: center;
}

@media (max-width: 500px) {
    .empty-list{
        font-size: 24px;
    }
}